.App {
  text-align: center;
  background: -webkit-gradient(linear,left top, left bottom,from(rgba(255,255,255,1)),to(rgba(255, 255, 255, 0.712))), url('https://opensignal.com/dist/images/pages/reports/auto/regions/usa.png');
  background: -webkit-linear-gradient(rgba(255,255,255,1),rgba(255, 255, 255, 0.712)), url('https://opensignal.com/dist/images/pages/reports/auto/regions/usa.png');
  background: -o-linear-gradient(rgba(255,255,255,1),rgba(255, 255, 255, 0.712)), url('https://opensignal.com/dist/images/pages/reports/auto/regions/usa.png');
  background: linear-gradient(rgba(255,255,255,1),rgba(255, 255, 255, 0.712)), url('https://opensignal.com/dist/images/pages/reports/auto/regions/usa.png');
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}
.title {
  margin-bottom: 0px;
}
.form-container {
  padding-left: 10px;
  padding-right: 10px;
}

.form-horizontal {
  /* Size and position */
  margin: 30px auto 30px;
  padding: 15px;

  /* Styles */
  background: #fffaf6;
  border-radius: 4px;
  color: #7e7975;
  -webkit-box-shadow:
      0 2px 2px rgba(0,0,0,0.2),        
      0 1px 5px rgba(0,0,0,0.2),        
      0 0 0 12px rgba(255,255,255,0.4);
          box-shadow:
      0 2px 2px rgba(0,0,0,0.2),        
      0 1px 5px rgba(0,0,0,0.2),        
      0 0 0 12px rgba(255,255,255,0.4); 
}
@media(min-width: 450px) {
  .form-horizontal {
    width: 342px;
  }
}
.form-control {
 width: 95%;
 margin: auto;
 margin-bottom: 10px;
}
legend {
 font-size: 15px;
 font-weight: bold;
 color: #6c6763;
 padding-right: 2px;
 padding-bottom: 8px;
 border-bottom: 1px solid #EBE6E2;
 text-shadow: 0 2px 0 rgba(255,255,255,0.8);
 -webkit-box-shadow: 0 1px 0 rgba(255,255,255,0.8);
         box-shadow: 0 1px 0 rgba(255,255,255,0.8);
 display: inline-block;
 text-transform: uppercase;
}
.btn{
margin-left: 1%;
 background: #fbd568; /* Fallback */
 background: -o-linear-gradient(#fbd568, #ffb347);
 background: -webkit-gradient(linear, 0 0, 0 100%, from(#fbd568), to(#ffb347));
 background: -webkit-linear-gradient(#fbd568, #ffb347);
 background: -webkit-gradient(linear, left top, left bottom, from(#fbd568), to(#ffb347));
 background: linear-gradient(#fbd568, #ffb347);
 border: 1px solid #f4ab4c;
 color: #996319;
 text-shadow: 0 1px rgba(255,255,255,0.3);
}
label {
 display: -ms-flexbox;
 display: flex;
 margin-left: 10px;
 margin-bottom: 10px !important;
 padding: 0 0 5px 2px;
 cursor: pointer;
 text-transform: uppercase;
 font-weight: 400;
 text-shadow: 0 1px 0 rgba(255,255,255,0.8);
 font-size: 11px;
} 



